@import '../../styles/extends.scss';
@import '../../styles/variables.scss';

.header {
  padding: 16px 0;
  position: sticky;
  top: 0;
  background-color: #fff;
  @media (max-width: 600px) {
    padding: 6px 0;
  }

  &__logo {
    @media (max-width: 600px) {
      width: 90px;
      height: 16px;
    }
  }

  &__wrapper {
    @extend %flex-row-center;

  }

  &__delivery-info {
    @extend %flex-row-center;
    margin-left: 7vw;

    @media (max-width: $break-point-tablet) {
      display: none;
    }
  }

  &__search {
    margin-left: auto;
    @media (max-width: $break-point-tablet) {
      display: none;
    }
  }

  &__link {
    margin-left: 40px;
    color: #1f1f1f;
    padding: 12px 0;
    white-space: nowrap;
    &:hover {
      text-decoration: underline;
    }
  }

  &__toggle-buttons {
    display: none;
    margin-left: auto;

    @media (max-width: $break-point-tablet) {
      display: flex;
    }
  }

  &__toggle-btn {
    background-color: transparent;
    border: 1px solid transparent;
    padding: 12px 7px 4px;
    cursor: pointer;

    &:not(:last-of-type) {
      margin-right: 20px;
    }
  }
}

.mobile-controls {
  display: none;
  position: relative;
  cursor: pointer;
  padding-top: 60px;
  padding-bottom: 10px;

  @media (max-width: $break-point-tablet) {
    display: flex;
  }

  &__close {
    background-color: transparent;
    border: 1px solid transparent;
    padding: 12px;
    cursor: pointer;
    position: absolute;
    top: 10px;
    right: 0;
  }
}

.mobile-controls--label-to:after {
  position: absolute;
  top: -23px;
  left: 0;
  content: "To";
  font-size: 11px;
  color: #626262;
  letter-spacing: 1px;
}

.mobile-controls--label-when:before {
  position: absolute;
  top: -23px;
  left: 0;
  letter-spacing: 1px;
  content: "When";
  font-size: 11px;
  color: #626262;
}
