@import '../../styles/variables.scss';

.restaurants-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(348px, 1fr));
  gap: 40px 20px;

  @media (max-width: $break-point-tablet) {
    row-gap: 20px;
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  }
}

.loader {
  text-align: center;
  margin-top: 30vh;
}
