@import "../../styles/extends";
@import "../../styles/variables";


.footer {
  background-color: #262626;
  padding: 64px 0;

  @media (max-width: $break-point-tablet) {
    padding: 40px 0;
  }

  &__top-part {
    margin-bottom: 40px;
    padding-bottom: 40px;
    border-bottom: 1px solid #c4c4c4;
  }

  &__logo-container {
    @extend %flex-row-center;
    margin-bottom: 40px;
    display: flex;

    @media (max-width: $break-point-tablet) {
      flex-direction: column;
      align-items: start;
    }
  }

  &__logo {
    margin-right: 40px;

    @media (max-width: $break-point-tablet) {
      margin-right: 0;
      margin-bottom: 40px;
    }
  }

  &__main {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
  }

  &__mobile-app {
    display: inline-block;
    &:first-of-type {
      margin-right: 20px;
      @media (max-width: $break-point-tablet) {
        margin-bottom: 20px;
      }
    }
    @media (max-width: 570px) {
      margin-bottom: 20px;
    }
  }

  &__mobile-apps {
    @media (max-width: $break-point-tablet) {
      display: flex;
      flex-direction: column;
    }
    @media (max-width: 570px) {
      flex-direction: row;
      margin: 10px 0;
    }
  }

  &__top-part,
  &__bottom-part {
    @extend %flex-row-center;
    align-items: flex-start;

    @media (max-width: 570px) {
      flex-flow: column;
      border-bottom: none;
      margin-bottom: 0;
      padding-bottom: 0;
    }
  }

  &__bottom-part {
    @media (max-width: 570px) {
      flex-direction: column-reverse;
    }
  }

  &__copyright,
  &__main {
    flex-basis: 45vw;
    padding-right: 40px;
  }

  &__top-links:last-of-type {
    margin-left: 10vw;
    @media (max-width: 570px) {
      margin-left: 0;
    }
  }

  &__copyright {
    font-size: 14px;
    color: #fff;
    @media (max-width: 570px) {
      flex-basis: 0;
    }
  }

  &__bottom-misc {
    display: flex;
    flex-direction: column-reverse;
  }

  &__bottom-links {
    @extend %flex-row-center;
    margin-bottom: 40px;

    .links__link:not(:last-of-type) {
      margin-right: 40px;
    }

    @media (max-width: 570px) {
      flex-direction: column;
      align-items: start;
    }
  }
}


.links {
  &__link {
    display: block;
    margin-bottom: 16px;
    color: #fff;
    white-space: nowrap;
    &:hover {
      text-decoration: underline;
    }
  }
}

.social__link {
  display: inline-block;
  margin-right: 20px;

  @media (max-width: 570px) {
    img {
      width: 20px;
      height: 20px;
      margin: 25px 0;
    }
  }
}
