@import '../../styles/extends.scss';
@import '../../styles/variables.scss';

.control {
  $border-color: #e0e0e0;
  @extend %flex-row-center;
  position: relative;
  padding: 11px 16px;
  border: 1px solid $border-color;
  transition: border-color 300ms;

  &--focused {
    border-color: darken($border-color, 20);
  }

  &__icon {
    width: 14px;
    height: 14px;
    margin-right: 10px;
  }

  &__input {
    max-height: 24px;
    line-height: 24px;

    &--small {
      @media (max-width: $break-point-tablet-lg) {
        max-width: 80px;
        max-height: 20px;
        line-height: 20px;
      }
    }

    @media (max-width: $break-point-tablet-lg) {
      max-width: 160px;
    }

  }

  & + & {
    margin-left: 20px;
  }
}
